/* General Styling */
.techstack {
    padding: 40px 10px;
    margin-left: 200px;
    background-color: rgba(128, 128, 128, 0.101);
    box-shadow: 0px 4px 8px 0px #757575;
}

.techstack h2 {
    font-weight: bold;
    color: #4d4c37;
    text-align: center; /* Center the heading */
}

.techstack hr {
    height: 4px;
    margin: 10px auto;
    background-color: #4d4c37;
    border: none;
}

.techstack p {
    color: black;
    text-align: center; /* Center the description text */
}

/* Card Styling */
.cardstext {
    display: flex;
    flex-wrap: wrap; 
    justify-content: center; 
    margin-left: 40px; /* Removed extra margin-left for mobile */
}

.tech-card {
    transition: box-shadow 0.3s ease-in-out;
    border: none;
    background: #ffffff;
    border-radius: 10px;
    overflow: hidden;
    cursor: pointer;
    margin: 10px;
    width: 200px; 
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
}

.tech-card:hover {
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.15);
    background: linear-gradient(135deg, #38a89d, #138781);
    color: white;
}

.tech-card:hover h5 {
    color: #ffffff;
}

/* Icon Styling */
.tech-icon {
    transition: transform 0.3s ease-in-out;
}

.tech-card:hover .tech-icon {
    transform: scale(1.2);
}

/* Responsive Design */

/* Tablet and Mobile View */
@media (max-width: 600px) {
    .techstack {
        padding: 0;
        margin: 0;
        padding-top: 40px;
    }

    .techstack h2 {
        font-size: 22px; /* Slightly smaller font for mobile */
    }

    .tech-card {
        width: 100%; /* Smaller card width */
        margin: 10px; /* Ensure cards are spaced properly */
    }

    /* Adjust the card text container */
    .cardstext {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: 0; /* Remove extra padding on smaller screens */
        padding: 0;
    }
}
