.navbar-profile-pic img{
    border-radius: 40px;
    height: 150px;
    width: 150px;
    display: flex;
    vertical-align: middle;
    margin: 0 auto;
    /* margin-top: 10px; */
}
.nav-item{
    font-family: 'Courier New, monospace';
    font-size: 18px;
    color: white;
    margin-top: 30px;
    /* text-align: center; */
    margin-left: 20px;
}
.nav-link{
/* margin-top: 20px; */
cursor: pointer;
color: white;
}
.nav-item svg{
    margin-right: 10px;
}
.nav-link .active{
    color: #fff2d0;
    text-decoration: none;
    padding-left: 10px;
    border-left: 5px solid #fff2d0;
}