/* General reset for horizontal overflow */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.about {
  padding: 40px 90px;
  margin-left: 200px;
  background-color: rgba(128, 128, 128, 0.101);
  box-shadow: 0px 4px 8px 0px #757575;
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

.about h2 {
  font-weight: bold;
  color: #4d4c37;
}

.about hr {
  height: 4px;
  margin: 10px auto;
  background-color: #4d4c37;
  border: none;
  width: 50%;
}

/* Container */
.image-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img {
  width: 350px;
  height: 400px;
  border-radius: 2%;
  object-fit: cover;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease-in-out;
}

.profile-img:hover {
  transform: scale(1.05);
}

/* Text Container */
.text-container {
  flex: 1;
  text-align: justify;
}

.info {
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.aboutcontext {
  font-family: 'Courier New, monospace';
  color: black;
  margin-top: 35px;
  font-size: 1.24rem;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 600px) {
  .about {
    padding: 0;
    margin: 0;
    padding-top: 30px;
    width: 100%;
    overflow-x: hidden; /* Prevent horizontal scrolling */
  }

  .profile-img {
    width: 270px;
    height: 350px;
    transform: translateY(-20px);
  }

  .text-container {
    text-align: center;
    margin-bottom: 30px;
    text-align: justify;
    width: 100%; /* Ensure text container fits within the screen */
    margin-left: 0;
    padding: 0px 10px;
  }

  .col-md-6, .col-xl-6, .col-lg-6, .col-xs-12 {
    width: 100%; /* Ensure columns take full width */
    margin-bottom: 20px; /* Add space between columns */
  }
}
