.mobile-nav{
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
}
.mobile-nav-header{
    height: 50px;
    width: 100%;
    background-color: #1e1e1e;
    padding: 8px;
}
.mobile-nav-title{
    color: #ffffff;
    margin-left: 20px;
    text-transform: capitalize;
    font-size: 25px;
    font-weight: bold;
    vertical-align: middle;
}
.mobile-nav-header .mobile-nav-icon{
    color: #ffffff;
    cursor: pointer;
    vertical-align: middle;
}
.mobile-nav-menu .nav-items{
    height: auto;
    width: 200px;
    background-color: #1e1e1e;
    padding: 10px;
    padding-bottom: 20px;
    border-bottom-right-radius: 10px;
}

@media (min-width:600px){
    .mobile-nav{    
        display:none;
    }
}