.education {
    margin: 50px 0px;
    padding: 50px 0px;
    /* align-items: center; */
    margin-left: 200px;
    background-color: rgba(128, 128, 128, 0.101);
    box-shadow: 0px 4px 8px 0px #757575;
}

.education h2 {
    font-weight: bold;
    color:  #4d4c37;
}

.education hr {
    height: 4px;
    margin: 10px auto;
    background-color: #4d4c37;
    border: none;
    width: 50%;

}
.edu_card{
    font-family: 'Courier New, monospace';
}
.education .vertical-timeline-element-title {
    color: black;
}
.education .vertical-timeline-element-subtitle{
    color: black;
}
.education  .educardpara{
    color: black;
}
@media (max-width: 600px){
    .education{
        padding: 0;
        margin: 0;
        padding-top: 40px;
    }
}