/* General Project Section */
.project {
    padding: 40px 10px;
    margin: 50px 0px;
    margin-left: 200px;
    background-color: rgba(128, 128, 128, 0.101);
    box-shadow: 0px 4px 8px 0px #757575;
}

.project h2 {
    font-weight: bold;
    color: #4d4c37;
}

.project hr {
    height: 4px;
    margin: 10px auto;
    background-color: #4d4c37;
    border: none;
    width: 50%;
}

.project p{
    color: black;
}
.project .cards{
    margin-left: 38px;
}
/* Project Cards */
.project .card-rounded {
    background: #fff;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    transition: box-shadow 0.3s ease-in-out;
    position: relative;
    cursor: pointer;
    /* width: 20vw; */
    /* max-width: 350px;  */
}

.card-rounded:hover {
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
}

/* Card Image */
.card-image {
    position: relative;
    overflow: hidden;
}

.card-image img {
    width: 100%;
    height: 150px;
    display: block;
    transition: transform 0.3s ease-in-out;
}

.card-rounded:hover .card-image img {
    transform: scale(1.1);
}

/* Notification Badge */
.card-notify-badge {
    position: absolute;
    top: 10px;
    left: 10px;
    background: #138781;
    color: #fff;
    font-size: 14px;
    padding: 5px 10px;
    border-radius: 5px;
}

/* Card Overlay */
.card-image-overlay {
    position: absolute;
    top: 153px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 5px;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}

.card-rounded:hover .card-image-overlay {
    opacity: 1;
}

.card-detail-badge {
    background: #ff6b6b;
    color: white;
    padding: 5px 10px;
    font-size: 12px;
    border-radius: 5px;
}

/* Card Body */
.card-body {
    text-align: center;
    padding: 15px;
}

.ad-title h5 {
    font-size: 18px;
    color: #333;
    margin-bottom: 10px;
}

/* View Button */
.ad-btn {
    display: inline-block;
    background: #138781;
    color: white;
    padding: 8px 20px;
    border-radius: 5px;
    text-decoration: none;
    transition: background 0.3s ease-in-out;
}

.ad-btn:hover {
    background: #0f6b66;
    color : white
}


/* responsive */
@media (max-width: 600px){
    .project{
        padding: 0;
        margin: 0;
        padding-top: 40px;
        
    }
    .cards{
       /* margin-right: 90px; */
       margin: 10px;
       width: 100%;
       transform: translateX(-48px);
    }
    .last-card{
        margin: 10px;
    }
}