.sidebar-section {
    display: flex;
    background-color: #4d4c37;
    /* border-bottom: 2px solid white; */
}

.sidebar {
    height: 100vh;
    background-color: #4d4c37;
    /* border-right: 2px solid white; */
    width: 90px;
    transition: width 0.3s ease; /* Smooth transition for width */
    position: fixed;
}

.sidebar-toggle {
    width: 230px;
}

.sidebar-toggle-icons {
    display: flex;
    align-items: right; /* Align items properly */
    justify-content: right;
    cursor: pointer;
}

.sidebar-toggle-icons p svg {
    background-color: black;
    color: white;
    margin-top: 30px;
    /* border-radius: 50%; Make the toggle icon circular */
    padding: 5px; /* Add padding for better click area */
}

/* Mobile View */
@media (max-width:600px){
    .sidebar{
        display: none;
    }
}