.home-container{
    height: 300px;
    color: white;
}
.home-content {
    font-family: 'Courier New, monospace';
    padding: 10px;
    padding-top: 80px;
    padding-left: 150px;
}
.home-buttons{
    margin-top: 15px;
}
.home-buttons .btn-hire{
    background: linear-gradient(90deg, #ff8a00, #e52e71, #5b2be0);
    background-size: 300% 300%;
    animation: gradientAnimation 5s infinite linear;
    color: white;
    padding: 12px 24px;
    font-size: 12px;
    font-weight: bold;
    border-radius: 30px;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    margin-right: 10px;
}
    
@keyframes gradientAnimation {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
}
    
.home-buttons .btn-hire:hover{
    transform: scale(1.1);
    box-shadow: 0px 0px 15px rgba(255, 138, 0, 0.5);
}
.home-buttons .btn-cv{
    background: linear-gradient(90deg, #ff8a00, #e52e71, #5b2be0);
    background-size: 300% 300%;
    animation: gradientAnimation 5s infinite linear;
    color: white;
    padding: 12px 24px;
    font-size: 12px;
    font-weight: bold;
    border-radius: 30px;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.home-buttons .btn-cv:hover{
    transform: scale(1.1);
    box-shadow: 0px 0px 15px rgba(255, 138, 0, 0.5);
}

.theme-btn{
    background-color: #1e1e2c;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 50px;
    margin-top: 50px;
    text-align: center;
    position: fixed;
    right: 0;
    cursor: pointer;
    z-index: 1;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}
    
    /*responsive home page */
@media (max-width: 600px){
    .home-content{
        padding: 0;
        margin: 0;
        padding-top: 110px;
    }
    
}
