.WorkExp {
    padding: 40px 0;
    background-color: rgba(128, 128, 128, 0.101);
    margin-left: 200px;
    box-shadow: 0px 4px 8px 0px #757575;
}
.WorkExp h2 {
    font-weight: bold;
    color: #4d4c37;
}

.WorkExp hr {
    height: 4px;
    margin: 10px auto;
    background-color: #4d4c37;
    border: none;
    width: 50%;
}
.WorkExp .vertical-timeline-element-title {
    color: black;
}
.WorkExp .vertical-timeline-element-subtitle{
    color: black;
}
.WorkExp  .educardpara{
    color: black;
}

/* Responsive */
@media (max-width: 600px){
    .WorkExp{
        padding: 0;
        margin: 0;
        padding-top: 40px;
        
    }
}