* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    background-color: #fff2d0;
}
.footer-text {
    color: black;
}
/* //Dark Mode */
#dark {
    background-color: #293230;
    color : white;
}
#dark .sidebar-section{
    background-color: #100c24;
}
#dark .sidebar{
    background-color: #100c24;
    
}
#dark .about h2 {
    color: #ffffff;
}
#dark .about hr{
    background-color: #ffffff;
}
#dark .aboutcontext {
    color: #ffffff;
}
#dark .education h2 {
    color: #ffffff;
}
#dark .education hr{
    background-color: #ffffff;
}
#dark .techstack h2 {
    color: #ffffff;
}
#dark .techstack hr{
    background-color: #ffffff;
}
#dark .techstack p{
    color: white;
}
#dark .project h2 {
    color: #ffffff;
}
#dark .project hr{
    background-color: #ffffff;
}
#dark .project p{
    color: white;
}
#dark .WorkExp h2 {
    color: #ffffff;
}
#dark .WorkExp hr{
    background-color: #ffffff;
}
#dark .footer{
    color: #ffffff;
}
#dark .techstack .tech-card{
    background-color: #ffffff;
    color: #100c24;
}

/* responsive */
@media (max-width: 600px){
    .footer{
        margin: 0;
        padding: 0;
        padding-top: 20px;  
        margin-bottom: 20px; 
    }
}