/* General reset for horizontal overflow */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .contact {
      padding: 40px 60px;
      margin: 40px 0px;
      margin-left: 200px;
      background-color: rgba(128, 128, 128, 0.101);
      box-shadow: 0px 4px 8px 0px #757575;
      overflow-x: hidden; /* Prevent horizontal scrolling */
  }
  
  .contact .card0{
    box-shadow: 0px 4px 8px 0px #757575;
      border-radius: 0px;
      background-color: #fff2d0;
  }
  
  .contact .card2{
      background-color: #fff2d0;
  }
  
  .contact .card .image{
      padding: 30px;
      height: 100%;
      width: 100%;
      margin-top: 30px;
  }
  
  .line{
      height: 1px;
      width: 45%;
      background-color: #e0e0e0e0;
      margin-top: 10px;
  }
  
  .or{
      display: flex;
      width: 10%;
      font-weight: bold;
  }
  
  input,textarea{
      padding: 10px 12px;
      border: 1px solid lightgray;
      width: 100%;
      color: #138781;
      font-size: 1rem;
      letter-spacing: 1px;
  }
  .contact-icons{
    font-size: 33px;
  }
  
  /* Dark Mode */
  #dark .card{
      background-color: rgba(128, 128, 128, 0.101);
  }
  
  /* Responsive */
  @media (max-width: 600px){
      .contact{
          padding: 0;
          margin: 0;
          padding-top: 40px;  
          overflow-x: hidden; /* Prevent horizontal scrolling */
      }
      .contact .card2 {
          margin: 0;
      }
      .contact .col-md-6, .contact .col-lg-6, .contact .col-xl-6 {
          width: 100%; /* Ensure full width of columns on small screens */
      }
      .contact .contact-icons{
        font-size: 23px;
      }
  }
  